.searchBox {
    width: 100vw;
    height: 100vh;
    max-width: 100%;

    display: flex;
    justify-content: center;
    align-items: center;

    background-color: rgb(231, 231, 231);

    /* position: fixed;
    top: 0%;
    left: 0; */
  }
  
  .searchBox > input[type="text"] {
    box-shadow: 0 0 5px rgba(0, 0, 0, 0.274);
    background-color: white;

    border: none;
    color: rgba(0, 0, 0, 0.637);

    padding: 1vmax 2vmax;

    width: 50%;

    outline: none;

    border-radius: 0%;

    font-weight: 300;
    font-size:  1.3vmax ;
    /* font-family: cursive; */
    height: 8%;
  }
  
  .searchBox > input[type="submit"] {
    height: 8%;
    border-radius: 0%;

    background-color: #097579;
    border: none;

    padding: 1vmax;
    
    width: 10%;

    font-weight: 300;
    font-size:  1.3vmax ;

    cursor: pointer;
    color: white;
    transition: all 0.5s;
  }
  
  .searchBox > input[type="submit"]:hover {
    background-color: #2372b3;
  }
  
  @media screen and (max-width: 600px) {
   
    .searchBox > input[type="text"] {
        margin-left: 20px;
      width: 100%;
      font-weight: 300;
      font-size:  4vw ;
      height: 8%;
    }
  
    .searchBox > input[type="submit"] {
        margin-right: 20px;
      height: 8%;
      width: 30%;
      font-weight: 300;
      font-size:  4vw ;
    }
  } 